import React, { useEffect, useRef, useState, forwardRef, useImperativeHandle } from "react";

import Chart from "chart.js";

export const JISU = 1;
export const HV = 2;
export const VOLA = 3;
export const HIGH = 4;
export const VIX = 5;

const Test = forwardRef((props, ref) => {
  const chartRef = useRef(null);
  const [chart, setChart] = useState();
  const [chartType, setChartType] = useState(props.chartType);

  //#region Export
  function updateChartData(_chartType, _lxAxisData) {
    if (chartType !== _chartType)
      return;

    if (chart !== undefined) {
      chart.config.data.datasets[0].data = _lxAxisData;
      chart.update();
    }
  };

  function initialChartData(_chartType, _yAxisData, _lxAxisData, _rxAxisData) {
    if (chartType !== _chartType)
      return;

    if (chart !== undefined) {
      chart.data.labels = _yAxisData;
      chart.config.data.datasets[0].data = _lxAxisData;
      chart.config.data.datasets[1].data = _rxAxisData;
      chart.update();
    }
  }

  useImperativeHandle(ref, () => ({
    updateChartData,
    initialChartData,
  }));
  //#endregion Export
  
  useEffect(() => {
    if (chartRef?.current) {
      const chartToDraw = chartRef.current.getContext("2d");

      const chart = new Chart(chartToDraw, {
        type: "line",
        data: {
          datasets: [{
              fill:false,
              borderColor: "#396AC1",
              yAxisID: 'A',
              borderWidth: 1,
            },
            {
              fill:false,
              borderColor: "#ED7B2E",
              yAxisID: 'B',
              borderJoinStyle: 'round',
              borderWidth: 1,
            }]
        },
        options: {
          legend: {
            display:false,
          },
          scales: {
            xAxes: [
              {
                id: 'X',
                ticks: {
                  callback: function(value, index, values) {
                    return value;
                  }
                }
              }
            ],
            yAxes: [{
              id: 'A',
              type: 'linear',
              position: 'left',
              ticks: {
                fontColor: '#323232',
                callback: function(value, index, values) {
                  return value + '';
                }
              },
            }, {
              id: 'B',
              type: 'linear',
              position: 'right',
              display:'auto',
              gridLines:false,
              minLabelPosition:0.1,
              ticks: {
                fontColor: '#323232',
                callback: function(value, index, values) {
                  return value + '';
                }
              }
            }]
          },
          annotation: {
            drawTime: "afterDatasetsDraw", // (default)
            events: ["click"],
            dblClickSpeed: 350, // ms (default)
            annotations: [
              {
                drawTime: "afterDraw",
                id: "a-line-1",
                type: "line",
                mode: "horizontal",
                scaleID: "y-axis-0",
                value: "25",
                borderColor: "red",
                borderWidth: 2,
              },
            ],
          },
        },
      });

      setChart(chart);
    }
    
  }, []);

  return (
    <>
      <div className={{ width: "80%", height: "50%" }}>
        <canvas id="myChart" ref={chartRef} />
      </div>
    </>
  );
});

export default Test;
